ign-items-center justify-content-center\"\r\n                    style=\"margin:20px 0\"\r\n                >\r\n                    <li>Dashboard</li>\r\n                </ul>\r\n\r\n                <ul\r\n                    class=\"vertical-navbar-options d-flex flex-column align-items-center justify-content-center\"\r\n                    style=\"width:100%\"\r\n                >\r\n                    <li\r\n                        class=\"left-menu-list d-flex justify-content-center align-items-center\"\r\n                        v-on:click=\"openTab(0)\"\r\n                    >\r\n                        View All\r\n                    </li>\r\n                    <li\r\n                        class=\"left-menu-list d-flex justify-content-center align-items-center\"\r\n                        v-on:click=\"openTab(1)\"\r\n                    >\r\n                        Modify\r\n                    </li>\r\n                    <li\r\n                        class=\"left-menu-list d-flex justify-content-center align-items-center\"\r\n                        v-on:click=\"openTab(2)\"\r\n                    >\r\n                        Delete\r\n                    </li>\r\n                </ul>\r\n            </div>\r\n        </div>\r\n\r\n        <div class=\"information-container d-flex flex-column\">\r\n            <modal-vue\r\n                class=\"deletePopup\"\r\n                @on-close=\"$vm2.close('modal-1')\"\r\n                name=\"modal-1\"\r\n                :headerOptions=\"{\r\n                    title: 'Want to delete this item?'\r\n                }\"\r\n                :footerOptions=\"{\r\n                    btn1: 'Cancel',\r\n                    btn2: 'Delete',\r\n                    btn2Style: {\r\n                        backgroundColor: 'red'\r\n                    },\r\n                    btn2OnClick: () => {\r\n                        doDelete();\r\n                    },\r\n                    btn1OnClick: () => {\r\n      