      >\r\n                                    <td>{{ cloth.id }}</td>\r\n                                    <td>{{ cloth.name }}</td>\r\n                                    <td>{{ cloth.price }}</td>\r\n                                    <td>{{ cloth.available }}</td>\r\n                                    <td>\r\n                                        {{ cloth.photo }}\r\n                                    </td>\r\n                                    <td>{{ cloth.category_id }}</td>\r\n                                    <td>{{ cloth.type_id }}</td>\r\n                                </tr>\r\n                            </tbody>\r\n                        </table>\r\n                    </div>\r\n                </div>\r\n                <div\r\n                    class=\"delete-table\"\r\n                    style=\"display:none\"\r\n                    v-bind:class=\"{ active: isDeleteActive }\"\r\n                >\r\n                    <div class=\"table-head\">\r\n                        <table class=\"table table-hover\">\r\n                            <thead class=\"thead-light\">\r\n                                <tr class=\"d-flex\">\r\n                                    <th scope=\"col\" style=\"width: 100px\">ID</th>\r\n                                    <th scope=\"col\" style=\"width: 300px\">\r\n                                        NOMBRE\r\n                                    </th>\r\n                                    <th scope=\"col\" style=\"width: 100px\">\r\n                                        PRECIO\r\n                                    </th>\r\n                                    <th scope=\"col\" style=\"width: 100px\">\r\n                                        SIZE\r\n                                    </th>\r\n                                    <th scope=\"col\" style=\"width: 130px\">\r\n                                        QUALITY\r\n                                    </th>\r\n                                    <th scope=\"col\" style=\"width: 150px\">\r\n                                        COUNTRY\r\n                                    </th>\r\n                                    <!-- <th scope=\"col\" style=\"width: 100px\">STOCK</th> -->\r\n                                    <th scope=\"col\" style=\"width: 150px\">\r\n                                        THUMBNAIL\r\n                                    </th>\r\n                                    <th scope=\"col\" style=\"width: 150px\">\r\n                                        CATEGORIA\r\n                                    </th>\r\n                                    <th scope=\"col\" style=\"width: 150px\">\r\n                                        TIPO\r\n                                    </th>\r\n                                    <th scope=\"col\">ACTION</th>\r\n                                </tr>\r\n                            </thead>\r\n                        </table>\r\n                    </div>\r\n                    <div class=\"table-body\">\r\n                        <table>\r\n                            <tbody>\r\n                                <tr\r\n                                    v-for=\"(cloth, index) in filteredList\"\r\n                                    :key=\"index\"\r\n                                >\r\n                                    <td style=\"width: 100px\">\r\n                                        {{ cloth.item.id }}\r\n                                    </td>\r\n                                    <td style=\"width: 300px\">\r\n                                        {{ cloth.item.name }}\r\n                                    </td>\r\n                                    <td style=\"width: 100px\">\r\n                                        {{ cloth.price }}\r\n                                    </td>\r\n                                    <td style=\"width: 100px\">\r\n                                        {{ cloth.size }}\r\n                                    </td>\r\n                                    <td style=\"width: 130px\">\r\n                                        {{ cloth.item.quality }}\r\n                                        <i class=\"fas fa-star\"></i>\r\n                                    </td>\r\n                                    <td style=\"width: 150px\">\r\n                                        {{ cloth.item.country }}\r\n                                    </td>\r\n\r\n                                    <td style=\"width: 150px\">\r\n                                        <img\r\n                                            class=\"thumbnail\"\r\n                                            :src=\"\r\n                                                `images/shop/${\r\n                                                    cloth.item.photo.split(\r\n                                                        ','\r\n                                                    )[1]\r\n                                                }.jpg`\r\n                                            \"\r\n                                        />\r\n                                    </td>\r\n\r\n                                    <td style=\"width: 150px\">\r\n                                        {{ cloth.item.category_id }}\r\n                                    </td>\r\n\r\n                                    <td v-if=\"cloth.item.type_id === 1\">\r\n                                        VINTAGE\r\n                                    </td>\r\n                                    <td v-if=\"cloth.item.type_id === 2\">\r\n                                        SELECTED\r\n                                    </td>\r\n                                    <td>\r\n                                        <button\r\n                                            class=\"remove-button\"\r\n                                            v-on:click=\"\r\n                                                showDeletePopup(id, index)\r\n                                            \"\r\n                                        >\r\n                                            REMOVE\r\n                                        </button>\r\n                                    </td>\r\n                                </tr>\r\n                            </tbody>\r\n                        </table>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<style scoped>\r\n* {\r\n    font-family: \"Roboto\" !important;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n.main-title {\r\n    font-weight: 700;\r\n    color: white;\r\n    padding: 10px;\r\n    width: 100%;\r\n    font-size: 16px;\r\n    position: relative;\r\n}\r\n\r\n/* Vertical Navbar */\r\n\r\n.vertical-navbar {\r\n    height: 100%;\r\n    width: 275px;\r\n    border-right: 1px solid #ee2a7b;\r\n    background-color: #ee2a7b;\r\n    position: fixed;\r\n    z-index: 2;\r\n}\r\n\r\n.profile-details {\r\n    height: 250px;\r\n    width: 100%;\r\n}\r\n\r\n.dashboard-options {\r\n    width: 100%;\r\n}\r\n\r\n.vertical-navbar-menu {\r\n    height: calc(100% - 250px);\r\n    width: 100%;\r\n    background-color: rgb(240, 240, 240);\r\n}\r\n.left-menu-list {\r\n    padding: 10px;\r\n    width: 100%;\r\n}\r\n.left-menu-list:hover {\r\n    cursor: pointer;\r\n    background-color: #ee2a7b;\r\n    color: white;\r\n}\r\n\r\n/* Information container */\r\n\r\n.information-container {\r\n    position: absolute;\r\n    padding: 20px;\r\n    top: 0px;\r\n    left: 275px;\r\n    width: calc(100% - 300px);\r\n    height: 100vh;\r\n    margin: 0 auto;\r\n    z-index: -1;\r\n}\r\n\r\n.delete-table,\r\n.modify-table,\r\n.view-all-table {\r\n    transition: 0.5s ease ease-in-out;\r\n}\r\n\r\n.information-title-container,\r\n.chart-container {\r\n    width: 100%;\r\n}\r\n\r\n.chart {\r\n    width: 250px;\r\n    height: 250px;\r\n    background-color: #f522e4;\r\n}\r\n\r\n/* View All Table */\r\n\r\n.view-all-table {\r\n    display: none;\r\n}\r\n\r\n.thumbnail {\r\n    width: 50px;\r\n}\r\n\r\ninput,\r\nform button {\r\n    margin-left: 10px;\r\n}\r\n\r\ninput:focus,\r\ninput:active {\r\n    box-shadow: none !important;\r\n    border: 1px solid #ee2a7b;\r\n}\r\n\r\nform button {\r\n    background: transparent;\r\n    border: 1px solid #3a3a3a;\r\n    color: #3a3a3a;\r\n}\r\n\r\nform button:focus,\r\nform button:active,\r\nform button:hover {\r\n    background: #ee2a7b;\r\n    border: 1px solid #ee2a7b;\r\n    color: white;\r\n    box-shadow: none !important;\r\n}\r\n\r\n.modify-table {\r\n    display: none;\r\n}\r\n\r\n.table-container {\r\n    border: 2px solid rgb(221, 221, 221);\r\n    margin: 50px auto;\r\n}\r\n\r\n.table-header {\r\n    overflow: hidden;\r\n    width: 100%;\r\n}\r\n\r\n.table-body {\r\n    height: 680px;\r\n    width: 100%;\r\n    overflow: auto;\r\n}\r\n\r\nthead tr th {\r\n}\r\n\r\nth,\r\ntd {\r\n    padding: 20px !important;\r\n    border-color: 2px solid rgb(221, 221, 221);\r\n}\r\n\r\ntr:hover {\r\n    background-color: #acacac;\r\n}\r\n\r\n.active {\r\n    display: block !important;\r\n    transition: 0.5s ease ease-in-out;\r\n}\r\n\r\n.modal-body {\r\n    padding: 20px;\r\n}\r\n\r\n.remove-button {\r\n    width: 100%;\r\n    border-radius: 3px;\r\n    padding: 8px;\r\n    background-color: #ee2a7b;\r\n    color: white;\r\n\r\n}\r\n</style>\r\n\r\n<script>\r\nimport Login from '../Login/Login';\r\n\r\nexport default {\r\n    name: \"Admin\",\r\n\r\n    data() {\r\n        return {\r\n            cloths: [],\r\n            isViewAllActive: true,\r\n            isModifyActive: false,\r\n            isDeleteActive: false,\r\n            tabType: 0,\r\n            selectedId: 0,\r\n            selectedIndex: 0,\r\n            itemSearched: null,\r\n            cloth: null\r\n        };\r\n    },\r\n    components:{\r\n        Login\r\n    },\r\n\r\n    mounted() {\r\n        axios\r\n            .get(`/api/shop`)\r\n            .then(response => (this.cloths = response.data))\r\n            .catch(error => console.log(error.response.data));\r\n    },\r\n\r\n    methods: {\r\n        openTab: function(tabType) {\r\n            switch (tabType) {\r\n                case 0:\r\n                    this.isViewAllActive = true;\r\n                    this.isModifyActive = false;\r\n                    this.isDeleteActive = false;\r\n                    break;\r\n                case 1:\r\n                    this.isViewAllActive = false;\r\n                    this.isModifyActive = true;\r\n                    this.isDeleteActive = false;\r\n                    break;\r\n                case 2:\r\n                    this.isViewAllActive = false;\r\n                    this.isModifyActive = false;\r\n                    this.isDeleteActive = true;\r\n                    break;\r\n            }\r\n        },\r\n\r\n        showDeletePopup: function(id, index) {\r\n            this.$vm2.open(\"modal-1\");\r\n            this.selectedId = id;\r\n            this.selectedIndex = index;\r\n        },\r\n\r\n        doDelete: function() {\r\n            axios\r\n                .post(`api/admin/${this.selectedId}`)\r\n                .then(response => console.log(response.data, \"de locos\"))\r\n                .catch(error => console.log(error.response.data));\r\n            this.cloths.splice(this.selectedIndex, 1);\r\n\r\n            this.$vm2.close(\"modal-1\");\r\n        }\r\n    },\r\n    computed: {\r\n        filteredList() {\r\n            if (this.itemSearched) {\r\n                return this.cloths.filter(cloth => {\r\n                    return cloth.item.name\r\n                        .toLowerCase()\r\n                        .includes(this.itemSearched.toLowerCase());\r\n                });\r\n            }\r\n            return this.cloths;\r\n        }\r\n    }\r\n};\r\n</script>\r\n"],"sourceRoot":""}]);
