class=\"details\">\r\n                    <div class=\"size\">\r\n                        <span class=\"d-title\">Size:</span>\r\n                        <br />\r\n                        <span>{{ item[0].size }}</span>\r\n                    </div>\r\n                    <br />\r\n                    <div class=\"quality\">\r\n                        <span class=\"d-title\"> Quality: </span>\r\n                        <br />\r\n\r\n                        <span\r\n                            >{{ item[0].item.quality }}\r\n                            <i class=\"fas fa-star\"></i\r\n                        ></span>\r\n                    </div>\r\n                    <br />\r\n\r\n                    <div class=\"price\">\r\n                        <span class=\"d-title\">Price:</span>\r\n                        <br />\r\n                        <span>{{ item[0].price }}</span>\r\n                    </div>\r\n                    <div class=\"collection\">\r\n                        <br />\r\n                        <span class=\"d-title\">Collection:</span>\r\n                        <br />\r\n                        <span v-if=\"item[0].item.type_id === 1\">\r\n                            VINTAGE\r\n                        </span>\r\n                        <span v-if=\"item[0].item.type_id === 2\">\r\n                            SELECTED\r\n                        </span>\r\n                    </div>\r\n\r\n                    <br />\r\n                </div>\r\n\r\n                <div class=\"type-description\" style=\"font-size:16px\">\r\n                    Los dosmiles siempre han sido criticados en el mundo de la\r\n                    moda por sus combinaciones arriesgadas y conjuntos\r\n                    estrambticos; pantalones de tiro bajo, mini-faldas,\r\n                    colores llamativos... Manticora.Vntg tiene como meta traer\r\n                    de vuelta esa esencia descarada y provocativa, devolviendo\r\n                    el valor a todas esa marcas de ropa con estilo propio que\r\n                    fueron devoradas por multinacionales. Miss Sixty, Morgan de\r\n                    toi, Lois, Baby Phat... Marcas que actualmente han perdido\r\n                    todo lo que fueron para perderse en un estilo comercial y\r\n                    descafeinado.<br />\r\n                    Nuestra labor es encontrar esas piezas nicas que marcaron\r\n                    una dcada, las cuales la mayora fabricaban en sus paises\r\n                    de origen ( y no en pases donde predomina la produccin\r\n                    masiva o la explotacin). Adems, Manticora.vntg ofrece\r\n                    tambin una seleccin de marcas de grandes diseadores que\r\n                    lo fueron todo en los dosmiles.\r\n                    <br />\r\n                    <br />\r\n                    Manticora aposta por experimentar y dejar al lado los\r\n                    estigmas creados por la ropa dividida por gneros y que cada\r\n                    cual arriesgue y provoque como ms le plazca.\r\n                </div>\r\n                <div class=\"cta-container\">\r\n                    <router-link\r\n                        class=\"d-flex justify-content-center align-items-center detail-button order-5\"\r\n                        :to=\"{\r\n                            name: 'Shop'\r\n                        }\"\r\n                        ><i class=\"fas fa-arrow-left\"></i>\r\n                    </router-link>\r\n                </div>\r\n            </div>\r\n            <div\r\n                class=\"media-container col-12  col-sm-12 col-lg-6  d-flex justify-content-center align-items-center row order-4\"\r\n            >\r\n                <div class=\"img-1 col-12  col-md-12 col-xl-6\">\r\n                    <img\r\n                        class=\"test\"\r\n                        :src=\"\r\n                            `images/shop/${\r\n                                item[0].item.photo.split(',')[0]\r\n                            }.jpg`\r\n                        \"\r\n                    />\r\n                </div>\r\n                <div class=\"img-2 col-12 col-md-12 col-xl-6\">\r\n                    <img\r\n                        class=\"test\"\r\n                        :src=\"\r\n                            `images/shop/${\r\n                                item[0].item.photo.split(',')[1]\r\n                            }.jpg`\r\n                        \"\r\n                    />\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n<style scoped>\r\n.main {\r\n    width: 100vw;\r\n    height: 100vh;\r\n}\r\n.main-container {\r\n    height: 100%;\r\n    width: 100%;\r\n    margin: 0 auto;\r\n}\r\n\r\n.information-container {\r\n    height: 100%;\r\n    padding: 75px;\r\n}\r\n.media-container {\r\n    height: 100%;\r\n    padding: 75px;\r\n    margin: 0 auto;\r\n}\r\n\r\n.details {\r\n    margin-top: 50px;\r\n}\r\n.detail-button {\r\n    background-color: #ee2a7c1e;\r\n    width: 100%;\r\n    padding: 10px;\r\n    margin-top: 10px;\r\n    transition: 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);\r\n}\r\n.detail-button:hover {\r\n    color: white;\r\n    background-color: #ee2a7c;\r\n}\r\n.detail-button:hover .fas {\r\n    color: white;\r\n}\r\n.fas {\r\n    color: #ee2a7b;\r\n    margin-left: 5px;\r\n    width: 18px;\r\n    height: 18px;\r\n}\r\n\r\nimg {\r\n    width: 100%;\r\n    margin-top: 10px;\r\n}\r\n\r\n.d-title,\r\n.title h1 {\r\n    font-weight: bold;\r\n    color: #ee2a7b;\r\n}\r\n\r\n@media (max-width: 786px) {\r\n    .main {\r\n        height: auto;\r\n    }\r\n    .information-container,\r\n    .media-container {\r\n        padding: 10px;\r\n    }\r\n}\r\n</style>\r\n<script>\r\nimport Logo from \"../Logo/Logo\";\r\nexport default {\r\n    name: \"ShopItemDetails\",\r\n\r\n    data() {\r\n        return {\r\n            item: [],\r\n            item_id: this.$route.params.id\r\n        };\r\n    },\r\n    components: {\r\n        Logo\r\n    },\r\n\r\n    async mounted() {\r\n        try {\r\n            this.item = (await axios.get(`/api/shop/${this.item_id}`)).data;\r\n            console.log(this.item);\r\n        } catch (err) {\r\n            console.log(err);\r\n        }\r\n    }\r\n};\r\n</script>\r\n"],"sourceRoot":""}]);
