on: relative;\r\n    right: -160px;\r\n    transition: 0.3s ease-in-out;\r\n    top: 10px;\r\n}\r\n\r\nul.menuOpen {\r\n    right: 43%;\r\n}\r\n\r\nul li {\r\n    text-align: right;\r\n    margin: 5px 0px;\r\n    color: var(--color);\r\n}\r\n\r\nul li:hover {\r\n    cursor: pointer;\r\n}\r\n\r\n/* Menu opened black layout */\r\n\r\n.menu-opened-layout {\r\n    position: absolute;\r\n    background-color: var(--bgColor);\r\n    width: 0vw;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 0;\r\n    transition: 0.3s ease-in-out;\r\n    z-index: 2;\r\n}\r\n.menu-opened-layout.layoutOpen {\r\n    width: 100vw;\r\n}\r\n</style>\r\n\r\n<script>\r\nexport default {\r\n    name: \"Menu\",\r\n\r\n    props: { menuColor: String, backgroundColor: String },\r\n\r\n    data: () => ({\r\n        isActive: false,\r\n        isMenuOpen: false,\r\n        isLayoutOpen: false\r\n    }),\r\n    computed: {\r\n        cssVars() {\r\n            return {\r\n                /* variables you want to pass to css */\r\n                \"--color\": this.menuColor,\r\n                \"--bgColor\": this.backgroundColor\r\n            };\r\n        }\r\n    },\r\n\r\n    methods: {\r\n        menuOpen: function() {\r\n            this.isActive = !this.isActive;\r\n            this.isMenuOpen = !this.isMenuOpen;\r\n            this.isLayoutOpen = !this.isLayoutOpen;\r\n        }\r\n    }\r\n};\r\n</script>\r\n"],"sourceRoot":""}]);
